@import url(https://fonts.googleapis.com/css2?family=Bree+Serif&family=Ultra&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{background-color:#303032;min-height:100vh;font-family:'Bree Serif', serif}.wrapper{margin:40px}.help{display:flex}.is-absolute{position:absolute;bottom:-20px}

.header__title{font-size:36px;text-align:center;padding-top:40px;font-family:'Ultra', serif}

.search__box{display:flex;align-items:center;justify-content:center;outline:0;border:0}.search__box:focus{outline:0;border:0}

.cards__list{display:grid;grid-template-columns:1fr repeat(12, minmax(auto, 60px)) 1fr;grid-gap:40px;padding:60px 0}@media (max-width: 1000px){.cards__list{grid-gap:10px}}@media (max-width: 700px){.cards__list{grid-gap:20px}}.cards__list--items{grid-column:2 / span 12;display:grid;grid-template-columns:repeat(12, minmax(auto, 60px));grid-gap:40px}@media (max-width: 1000px){.cards__list--items{grid-template-columns:repeat(12, 1fr);grid-column-end:2 / span 6;grid-gap:20px}}

.card{background-color:white;grid-column-end:span 4;display:flex;flex-direction:column;cursor:pointer;transition:all, 0.3s ease 0s}.card:hover{transform:translateY(-4px)}.card__image-container{padding-top:30%;position:relative}@media (max-width: 500px){.card__image-container{display:flex;align-items:center;justify-content:center}}.card__image-container img{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.card__content{padding:0 20px 20px}.card__title{margin-bottom:20px;text-align:center;font-size:32px;font-family:'Ultra', serif}.card__info{display:flex;align-items:center;align-self:end}.card__info span{margin-left:10px;font-weight:bold}@media (max-width: 1000px){.card{grid-column-end:span 6}}@media (max-width: 700px){.card{grid-column-end:span 12}}

.modalw{height:100vh;width:100vw;background-color:rgba(0,0,0,0.7);position:fixed;z-index:100;top:0;left:0;display:flex;align-items:center;justify-content:center}.modal__content{padding:20px;border-radius:4px;background-color:white;width:320px}.intro{display:flex;flex-direction:column;cursor:pointer}.intro__title{font-family:'Bree Serif', serif;display:flex;justify-content:space-between;margin:4px}.intro__timezone{font-family:'Ultra', serif;text-align:center;font-size:32px}.intro__content{font-size:16px}

